} */

.list li {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  background-color: var(--gray500);
  margin-bottom: 1rem;
  border: 1px solid var(--gray400);
  border-radius: 0.5rem;
}

.list li > p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding: 1rem;
}

.list li i {
  display: inline-block;
  padding: 1rem;
  transition: 0.5s;
}

.list li i:hover {
  color: var(--danger);
}

.popup {
  /* background-color: var(--gray100); */
  background-color: rgba(255,255,255,0.5);
  backdrop-filter: blur(5px);
  padding: 3rem;
  color: var(--gray700);
  position: fixed;
  top:0;
  left: 0;
  right: 0;
  bottom: 0;
}